class YMNodeManager extends Core {
  constructor(options) {
    super(options);
    this.root = null;
    this.YMNodes = {};
    this.YMNodesArray = [];
    this.nodesRenderArray = [];
    this.opts = options || {};
    this.expandNode = null;
    this.lineType = 'default';
  }

  parseData(sourceData) {
    this.switchParse(sourceData);
    return this;
  }

  switchParse({ format, data }) {
    format = format || 'node_array';
    switch (format) {
      case 'node_array':
      default:
        return this.parseArr(data);
    }
  }

  handleToggleLayout(node) {
    this.expandNode = node;
    this.layout();
    this.loadStyle();
    this.loadExpanderStyle();
    this.getLines();
    this.getNodes();
  }

  handleLayoutWithStratgy(strategy, lineType) {
    this.injectStrategy(strategy, lineType);
    this.layout();
    this.loadStyle();
    this.loadExpanderStyle();
    this.getNodes();
    this.getLines();
  }

  parseArr(nodesRenderArray) {
    const YMNodes = {};
    const YMNodesArray = [];
    let rootOrder = -1;
    for (let i = 0; i < nodesRenderArray.length; i++) {
      let node = nodesRenderArray[i];
      let ymnode = new YMNode(node);
      YMNodes[node.id] = ymnode;
      if (node.id === 'root') {
        YMNodesArray.push(ymnode);
        ymnode.order = rootOrder;
      } else if (YMNodes[node.parentid]) {
        ymnode.parent = YMNodes[node.parentid];
        if (ymnode.parent.id === 'root') {
          ymnode.order = ++rootOrder;
        } else {
          ymnode.order = ymnode.parent.order;
        }
        ymnode.index = ymnode.parent.index + 1;
        ymnode.parent.children.push(ymnode);
        YMNodesArray.push(ymnode);
      } else {
        delete YMNodes[node.id];
      }
    }
    this.YMNodes = YMNodes;
    this.YMNodesArray = YMNodesArray;
    this.root = this.YMNodes.root;
  }

  injectStrategy(strategy, lineType) {
    this.opts.strategy = strategy || this.opts.strategy || 'logic_right';
    this.lineType = lineType || this.lineType;
    //  root  root 
    layoutStrategyMap[this.opts.strategy](this.root);
  }

  layout() {
    const root = this.root;
    root.doLayout();
    childStrategy.rootRender(root);
  }

  loadStyle() {
    // topleftYMNodestyle
    const { root } = this;
    root.style = {
      top: root.ruleArea.start.y,
    };
    switch (this.opts.strategy) {
      case 'logic_left': {
        root.style = {
          left: root.ruleArea.width - root.selfArea.width + CanvasPadding,
        };
        break;
      }
      case 'logic_right': {
        root.style = {
          left: 0,
        };
        break;
      }
      case 'logic_left_right': {
        root.style = {
          left: root.childrenArea.partArea.left.width + rhs_lr + CanvasPadding,
          // top: (root.ruleArea.height - root.selfArea.height) / 2,
        };
        break;
      }
      default: {
        root.style = {
          left: root.ruleArea.start.x + root.selfArea.start.x,
        };
      }
    }
    const setStyle = node => {
      if (node.parent) {
        node.style = {
          left:
            parseFloat(node.parent.style.left) +
            node.parent.childrenArea.start.x +
            node.ruleArea.start.x +
            node.selfArea.start.x,
          top:
            parseFloat(node.parent.style.top) +
            node.parent.childrenArea.start.y +
            node.ruleArea.start.y +
            node.selfArea.start.y,
        };
      }
    };
    this.recursiveTraverse(this.root, setStyle);
  }

  loadExpanderStyle() {
    const { root } = this;
    // toggle
    this._doAll(root, Node => {
      if (Node.children.length === 0) {
        Node.expanderStyle = {
          visibility: 'hidden',
        };
      } else if (Node.style && Node.style.visibility === 'hidden') {
        // ignore
      } else {
        Node.expanderStyle = {
          visibility: 'visible',
        };
      }
    });

    // toggle
    expanderLS[this.opts.strategy](this);
  }

  getNodes() {
    // YMNodesArraynodesRenderArraynodesRenderArrayplain object
    //  this.nodesRenderArray = this.YMNodesArray.map(YMNode => {
    //   let node = { ...YMNode._source };
    //   node.style = YMNode.style;
    //   node.expanderStyle = YMNode.expanderStyle;
    //   node.expanded = YMNode.expanded;
    //   node.index = YMNode.index;
    //   return node;
    // });

    let tempNodes = [];
    let YMNodesArray = [];
    let YMNodes = {};
    const flatternNestdObject = Node => {
      const {
        // eslint-disable-next-line no-unused-vars
        children,
        source,
        style,
        expanderStyle,
        expanded,
        index,
        parent,
      } = Node;
      let node = { ...source };
      node.style = style; // 
      node.customStyle = { ...source.customStyle };
      getStyle(Node, node, this.lineType);
      node.expanderStyle = expanderStyle;
      node.expanded = expanded;
      node.index = index;
      node.parentid = parent && parent.id;
      tempNodes.push(node);
      YMNodesArray.push(Node);
      YMNodes[node.id] = Node;
    };
    this.recursiveTraverse(this.root, flatternNestdObject);
    this.nodesRenderArray = tempNodes;
    this.YMNodesArray = YMNodesArray;
    //  weakMap  weakMap 
    //  set  set  delete 
    this.YMNodes = YMNodes;
    return this.nodesRenderArray;
  }

  recursiveTraverse(node, action) {
    action(node);
    if (!node || !node.children) {
      return;
    }
    node.children.forEach(item => {
      this.recursiveTraverse(item, action);
    });
  }

  getLines() {
    this.linesArray = lineStrategy[this.opts.strategy](this).filter(e => e != null);
  }

  _doAll(node, cb) {
    cb(node);
    this._doAllChildren(node, cb);
  }

  _doAllChildren(node, cb) {
    if (node.children.length === 0) return;
    for (let i = 0; i < node.children.length; i++) {
      let child = node.children[i];
      cb(child);
      this._doAllChildren(child, cb);
    }
  }

  _getAllChildren(node) {
    if (node.children.length === 0) return [];
    let res = [];
    for (let i = 0; i < node.children.length; i++) {
      let child = node.children[i];
      res.push(child);
      res.push(...this._getAllChildren(child));
    }
    return res;
  }

  _getRootPartChildren(postion) {
    const partChildren = this.root.childrenArea.partArea[postion].YMNodesArray;
    if (!partChildren) return [];
    let partAllChildren = [];
    for (let i = 0; i < partChildren.length; i++) {
      const c = partChildren[i];
      partAllChildren.push(...this._getAllChildren(c));
    }
    partAllChildren.push(...partChildren);
    return partAllChildren;
  }

  export2xmind() {
    return getXmindData(this.root);
  }
}

export default YMNodeManager;
